import { Button, Col, Form, Input, Modal, Spin } from 'antd';
import { useForm } from 'antd/lib/form/Form';
import { EpicSelect } from 'components/EpicSelect';
import { TaskTypeSelect } from 'components/TaskTypeSelect';
import { UserSelect } from 'components/UserSelect';
import { useEffect } from 'react';
import {
  deleteTaskAsync,
  editTaskAsync,
} from 'redux/entities/task.slice';
import { useAppDispatch } from 'redux/store';
import { useTaskModal } from '../hooks/useTaskModal';

const formLayout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 16,
  },
};

export const TaskModal = () => {
  const { isOpen, editingTask, close, isLoading, mutateLoading } =
    useTaskModal();
  const [form] = useForm();
  const dispatch = useAppDispatch();

  useEffect(() => {
    editingTask && form.setFieldsValue(editingTask);
  }, [editingTask, form]);

  const handleClose = () => {
    close();
    form.resetFields();
  };

  const handleOk = () => {
    dispatch(
      editTaskAsync({
        ...editingTask,
        ...form.getFieldsValue(),
      }),
    )
      .unwrap()
      .then(() => {
        handleClose();
      });
  };

  const handleDeleteBtnClick = () => {
    if (!editingTask) return;

    Modal.confirm({
      title: `${editingTask.name}`,
      okText: '',
      cancelText: '',
      onOk() {
        return dispatch(deleteTaskAsync(editingTask.id))
          .unwrap()
          .then(() => {
            handleClose();
          });
      },
    });
  };

  return (
    <Modal
      visible={isOpen}
      title=""
      okText=""
      cancelText=""
      onCancel={handleClose}
      onOk={handleOk}
      confirmLoading={mutateLoading}
    >
      {isLoading ? (
        <div style={{ textAlign: 'center' }}>
          <Spin />
        </div>
      ) : (
        <Form form={form} {...formLayout}>
          <Form.Item
            label=""
            name="name"
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item label="" name="epicId">
            <EpicSelect defaultOptionName="" />
          </Form.Item>
          <Form.Item label="" name="processorId">
            <UserSelect defaultOptionName="" />
          </Form.Item>
          <Form.Item
            label=""
            name="typeId"
            rules={[{ required: true, message: '' }]}
          >
            <TaskTypeSelect />
          </Form.Item>
          <Col span="22" style={{ textAlign: 'right' }}>
            <Button onClick={handleDeleteBtnClick} size="small">
              
            </Button>
          </Col>
        </Form>
      )}
    </Modal>
  );
};
