interface IMarkWord {
  name: string;
  keyword?: string;
}

export const MarkWord = ({ name, keyword }: IMarkWord) => {
  if (!keyword) {
    return <span>{name}</span>;
  }

  const wordAry = name.split(keyword); //keyowrd

  return (
    <>
      {wordAry.map((str, index) => {
        return (
          <span key={index}>
            {/* str */}
            {str}
            {/* keyword */}
            {index === wordAry.length - 1 ? null : (
              <span style={{ color: '#257AFD' }}>{keyword}</span>
            )}
          </span>
        );
      })}
    </>
  );
};
